export const Colors = addSourceDecorator(() => ({
  components: {
    ColorSwatch,
    FlexRow,
    PageTitle,
    FlexColumn,
  },
  data: function() {
    return {
      IntusFacultas: Theme,
      text: TextTheme,
      niws: NIWSTheme,
      toast: AlertTheme,
      count: 0,
      limit: 3,
    };
  },
  methods: {
    splitArray: function(theme, limit) {
      let self = this;
      let colors = [];
      let count = 0;
      let row = 0;
      for (const key in theme) {
        if (count == 0) {
          colors.push([
            {
              name: key,
              color: theme[key].background,
            },
          ]);
        } else {
          colors[row].push({
            name: key,
            color: theme[key].background,
          });
        }
        count++;
        if (count == limit) {
          row += 1;
          count = 0;
        }
      }
      return colors;
    },
  },
  computed: {
    keys: function() {
      var self = this;
      return Object.keys(self.IntusFacultas);
    },
  },
  template: `
        <div>
            <page-title>Available Colors</page-title>
            <hr>
            <flex-row v-for="list in splitArray(IntusFacultas, limit)" style="padding-top:10px">
                <flex-column>
                    <div v-for="obj in list" style="display: flex">
                        <color-swatch
                            :name="obj.name"
                            :color="obj.color.color">
                            <color-swatch
                        </color-swatch>
                        <color-swatch
                            :name="obj.name + ':Hover'"
                            :color="obj.color.hover">
                            <color-swatch
                        </color-swatch>
                    </div>
                </flex-row>
                </flex-column>
            <page-title>NIWS Colors</page-title>
            <hr/>
            <flex-row v-for="list in splitArray(niws, 5)" style="padding-top:10px">
                <flex-column>
                    <div v-for="obj in list" style="display: flex">
                        <color-swatch
                            :name="obj.name"
                            :color="obj.color.color">
                            <color-swatch
                        </color-swatch>
                    </div>
                </flex-column>
            </flex-row>
            <page-title>Text Colors</page-title>
            <hr/>
            <flex-row style="padding-top:10px">
                <flex-column>
                    <color-swatch 
                        v-for="obj in Object.keys(text)"
                        :key="obj"
                        :name="obj"
                        :color="text[obj].color">
                    </color-swatch>
                </flex-column>
            </flex-row>
            <page-title>Toast Colors</page-title>
            <hr/>
            <flex-row v-for="list in splitArray(toast, 4)" style="padding-top:10px">
                <flex-column>
                    <div v-for="obj in list" style="display: flex">
                        <color-swatch
                            :name="obj.name"
                            :color="obj.color.color">
                            <color-swatch
                        </color-swatch>
                    </div>
                </flex-column>
            </flex-row>
        </div>
    `,
}), {__STORY__, __ADDS_MAP__,__MAIN_FILE_LOCATION__,__MODULE_DEPENDENCIES__,__LOCAL_DEPENDENCIES__,__SOURCE_PREFIX__,__IDS_TO_FRAMEWORKS__});
