export const RawInputNavbar = addSourceDecorator(() => ({
  components: {
    RawNavbar,
    NavbarContent,
    VueNavbarDropdown,
    NavbarItem,
    TextContent,
    List,
    ListItem,
  },
  data() {
    return {
      navbar: { collapsed: false },
      formattedTheme,
      items: [
        {
          type: "item",
          text: "Dropdown Item 1",
          url: "#",
          disabled: false,
        },
        {
          type: "item",
          text: "Dropdown Item 2",
          url: "#",
          disabled: false,
        },
      ],
    };
  },
  mounted() {
    let self = this;
    this.$nextTick(function() {
      self.$forceUpdate();
      self.navbar = self.$refs.navbar;
    });
  },
  props: {
    fixed: {
      default: boolean("Fixed to Top", false),
    },
    flavor: {
      default: text("Flavor", ""),
    },
    title: {
      default: object("Title", {
        html: "",
        text: "Brand",
        url: "#",
      }),
    },
  },
  template: `
        <div>
        <raw-navbar
            :title="title"
            :fixed="fixed"
            :flavor="flavor"
            ref="navbar"
            >
                <navbar-content>
                    <navbar-item>
                    <vue-navbar-dropdown
                      :parent="navbar"
                      icon=""
                      text="Dropdown" 
                      :items="items"
                      :flavor="flavor"
                    ></vue-navbar-dropdown>
                    </navbar-item>
                    <navbar-item>
                        <a href="#">Link 1</a>
                    </navbar-item>
                    <navbar-item>
                    <a href="#">Link 2</a>
                    </navbar-item>
                    <navbar-item>
                    <a href="#">Link 3</a>
                    </navbar-item>
                    <navbar-item>
                    <a href="#">Link 4</a>
                    </navbar-item>
                </navbar-content>
                <navbar-content>
                    <navbar-item>
                        <input>
                    </navbar-item>
                </navbar-content>
            </raw-navbar>
            <h1>Content! Stuff! Excitement!</h1>
            <hr>
            <text-content :size="16">Design system information can be found <a href="/?path=/story/design-system--colors">here</a></text-content><br>
            <text-content :size="16">Available Flavors</text-content>
            <list>
            <list-item v-for="themeFlavor in formattedTheme" :key="themeFlavor.text" :style="{color: themeFlavor.value}">
                {{themeFlavor.text}}
            </list-item>
            </list>
        </div>
    `,
}), {__STORY__, __ADDS_MAP__,__MAIN_FILE_LOCATION__,__MODULE_DEPENDENCIES__,__LOCAL_DEPENDENCIES__,__SOURCE_PREFIX__,__IDS_TO_FRAMEWORKS__});;

